import Datepicker from 'vuejs-datepicker'
import {en, bg} from 'vuejs-datepicker/dist/locale'
import {FX_RATES_TYPES} from '@/nomenclaturesConstants'
import { ModelSelect } from 'vue-search-select'

export default {
  components: {
    ModelSelect,
    Datepicker,
  },
  props: {
    averageFxRates: {},
  },
  data() {
    return {
      locales: {
        en: en,
        bg: bg,
      },
      formError: '',
      formData: {
        currencyId: null,
        recordType: this.averageFxRates ? 2 : 1,
      },
      convertedCurrencies: [],
    }
  },
  mounted() {
    const self = this
    this.$store.dispatch('loadCurrencies')
      .then(loadedItems => {
        loadedItems.forEach((currency, i) => {
          if (self.companyCurrency.id !== currency.id) {
            self.convertedCurrencies.push({
              value: currency.id,
              text: currency.code,
            })
          }
        })
      })
  },
  computed: {
    companyCurrency() {
      const company = this.$store.getters.selectedCompany || {}
      return company.currency
    },
    selectedCurrency() {
      if (this.formData.currencyId) {
        return ' for 1 ' + this.convertedCurrencies.find(currency => currency.value === this.formData.currencyId).text
      } else {
        return ''
      }
    },
    formRecordTypeDaily() {
      return this.formData.recordType === 1
    },
    fxRecordsTypes() {
      let types = []
      Object.keys(FX_RATES_TYPES).map((key) => {
        types.push(FX_RATES_TYPES[key])
      })
      return types
    },
    formHasError() {
      return !!this.formError.length
    },
  },
  methods: {
    onSelect() {
      alert('Bai Hui')
    },
    resetForm() {
      this.formError = ''
      this.formData = {
        currencyId: null,
        recordType: 1,
      }
      this.updateParent()
    },
    showError(error) {
      this.formError = error.messageDetail
    },
    updateParent() {
      this.$emit('updateFormData', this.formData)
    },
  },
}



