import merge from 'lodash.merge'

import companyService from '@/services/companyService'
import aliasService from '@/services/aliasService'
import personnelService from '@/services/personnelService'
import vatService from '@/services/vatService'
import companyPropertyService from '@/services/companyPropertyService'
import recurringDatesService from '@/services/recurringDatesService'

import {COMPANY_PROPERTY_CATEGORIES, RECURRING_DATE_TYPES} from '@/nomenclaturesConstants'
import findIndex from 'lodash.findindex'

const INITIAL_STATE = {
  myCompanies: [],
  selectedCompany: null,
}

var state = {...INITIAL_STATE}

var getters = {}

// Generate getters for constants
Object.keys(state).reduce((obj, key) => {
  obj[key] = (state) => state[key]
  return obj
}, getters)


const mutations = {
  setMyCompanies(state, payload) { state.myCompanies = payload },
  setCompanyNotificationsCount(state, payload) {
    var len = state.myCompanies.length
    for (var i = 0; i < len; i++) {
      var lenNotif = payload.length
      for (var y = 0; y < lenNotif; y++) {
        var myComp = state.myCompanies[i]
        if (myComp.id === payload[y].id) {
          myComp.notificationsCount = +payload[y].count
          payload.splice(y, 1)
          lenNotif--
          break
        }
      }
      if (lenNotif === 0) {
        break
      }
    }
  },

  // CRUD
  addCompany(state, payload) {
    // TODO: push after its parent?
    state.myCompanies.push(payload)
  },
  setSelectedCompany(state, payload) {
    state.selectedCompany = payload
    if (state.myCompanies) {
      const index = findIndex(state.myCompanies, company => company.id === payload.id)
      if (index > -1) { state.myCompanies[index] = payload }
    }
  },
  deleteSelCompany(state) {
    if (state.myCompanies) {
      const index = state.myCompanies.indexOf(state.selectedCompany)
      if (index > -1) { state.myCompanies.splice(index, 1) }
    }
    state.selectedCompany = null
  },

  // SELECTED COMPANY PROPERTIES

  setSCLogo(state, payload) {
    console.log('setSCLogo: ', payload)
    state.selectedCompany.logo.URL = payload
  },

  // ALIASES
  addSCAlias(state, payload) { state.selectedCompany.aliases.push(payload) },
  updateSCAlias(state, {alias, updatedAname}) {
    var aliases = state.selectedCompany.aliases
    const index = aliases.indexOf(alias)
    aliases[index].aname = updatedAname
  },
  deleteSCAlias(state, payload) {
    var aliases = state.selectedCompany.aliases
    const index = aliases.indexOf(payload)
    aliases.splice(index, 1)
  },
  // VATs
  addSCVat(state, payload) {
    state.selectedCompany.vats.push(payload)
  },
  updateSCVat(state, payload) {
    var vats = state.selectedCompany.vats
    const index = findIndex(vats, item => item.id === payload.id)
    vats[index] = payload
  },
  deleteSCVat(state, payload) {
    var vats = state.selectedCompany.vats
    const index = vats.indexOf(payload)
    vats.splice(index, 1)
  },

  // PERSONNEL
  addSCPersonnel(state, payload) { state.selectedCompany.personnel.push(payload) },
  updateSCPersonnel(state, payload) {
    var personnel = state.selectedCompany.personnel
    const index = findIndex(personnel, item => item.id === payload.id)
    personnel[index] = payload
  },
  deleteSCPersonnel(state, payload) {
    var personnel = state.selectedCompany.personnel
    const index = personnel.indexOf(payload)
    personnel.splice(index, 1)
  },

  // COMPANY PROPERTY PAIR
  addSCCPPair(state, payload) {
    var catName = getCategoryName(payload)
    state.selectedCompany[catName].push(payload)
  },
  updateSCCPPair(state, payload) {
    var catName = getCategoryName(payload)
    var category = state.selectedCompany[catName]
    const index = findIndex(category, item => item.id === payload.id)
    category[index] = payload
  },
  deleteSCCPPair(state, payload) {
    var catName = getCategoryName(payload)
    var category = state.selectedCompany[catName]
    const index = category.indexOf(payload)
    category.splice(index, 1)
  },
}

function getCategoryName(cpPair) {
  var catName = Object.keys(COMPANY_PROPERTY_CATEGORIES).find(item => COMPANY_PROPERTY_CATEGORIES[item] === cpPair.category.id)
  if (catName === 'tax') {
    catName = 'taxes'
  }
  return catName
}

function thenHandler(commit, commitName) {
  return function(res) {
    commit(commitName, res)
    return res
  }
}

const actions = {
  resetCompanies({ state }) {
    Object.assign(state, INITIAL_STATE)
  },

  loadMyCompanies({ commit, state }) {
    // if (state.myCompanies) {
    //   return Promise.resolve(state.myCompanies)
    // }

    return companyService.getMyCompanies()
      .then(thenHandler(commit, 'setMyCompanies'))
  },

  // CRUD
  createCompany({ commit, state }, companyData) {
    return companyService.create(companyData)
      .then(thenHandler(commit, 'addCompany'))
      .then(company => {
        if (companyData.logoData !== null) {
          return companyService.uploadLogo(company, companyData.logoData)
            .then(thenHandler(commit, 'setSCLogo'))
            .then(() => {
              return company
            })
        }
        return company
      })
  },
  loadSelCompany({ commit, state }, id) {
    if (state.selectedCompany && state.selectedCompany.id === id) {
      return Promise.resolve(state.selectedCompany)
    }

    return companyService.getCompany({id})
      .then(thenHandler(commit, 'setSelectedCompany'))
  },
  reloadSelCompany({ commit, state }, id) {
    return companyService.getCompany({id})
      .then(thenHandler(commit, 'setSelectedCompany'))
  },
  updateSelCompany({ commit, state }, updatedData) {
    var currentCorpTax = state.selectedCompany.corpTax
    var newData = merge({}, updatedData)
    var newRDatesPromises = []

    if (currentCorpTax && newData.corpTax) {
      // Since non of the fields of corpTax is required and because they use different APIs
      // then do the appropriate action for each individual property - create, update, delete.

      // taxNumber - it will be updated with the whole company object, so we don't do anything special here
      // recurrring dates - check for each of them for create, update, delete
      // fillingDate
      // dueDate
      // advancedPayments

      if (!currentCorpTax.fillingDate && newData.corpTax.fillingDate) {
        // Create new
        // newCorpTax.fillingDate.targetId = state.selectedCompany.id
        let fDate = newData.corpTax.fillingDate
        delete newData.corpTax.fillingDate
        fDate.companyId = state.selectedCompany.id
        fDate.typeId = RECURRING_DATE_TYPES.CORPTAX_FILLING_DATE.id
        fDate.holidayHandlingTypeId = 0
        newRDatesPromises.push(recurringDatesService.create(fDate))
      }

      if (!currentCorpTax.dueDate && newData.corpTax.dueDate) {
        // Create new
        let dueDate = newData.corpTax.dueDate
        delete newData.corpTax.dueDate
        dueDate.companyId = state.selectedCompany.id
        dueDate.typeId = RECURRING_DATE_TYPES.CORPTAX_DUE_DATE.id
        dueDate.holidayHandlingTypeId = 0
        newRDatesPromises.push(recurringDatesService.create(dueDate))
      }

      if (!currentCorpTax.advancedPayments && newData.corpTax.advancedPayments) {
        // Create new
        let apDate = newData.corpTax.advancedPayments
        delete newData.corpTax.advancedPayments
        apDate.companyId = state.selectedCompany.id
        apDate.typeId = RECURRING_DATE_TYPES.CORPTAX_ADVANCED_PAYMENTS_DATE.id
        apDate.holidayHandlingTypeId = 0
        newRDatesPromises.push(recurringDatesService.create(apDate))
      }
    } // end of newCorpTax

    return Promise.all(newRDatesPromises)
      .then(() => {
        return companyService.update(state.selectedCompany, newData)
      })
      .then(company => {
        commit('setSelectedCompany', company)
        console.log('STORE company: ', company)
        return company
      })
  },
  deleteSelCompany({ commit, state }) {
    return companyService.delete(state.selectedCompany)
      .then(() => {
        commit('deleteSelCompany')
      })
  },

  // SELECTED COMPANY PROPERTIES

  deleteSCCorpTax({ commit, state }) {
    var corpTax = state.selectedCompany.corpTax
    var rDatesPromises = []

    if (corpTax.fillingDate && corpTax.fillingDate.id) { rDatesPromises.push(recurringDatesService.remove(corpTax.fillingDate.id)) }
    if (corpTax.dueDate && corpTax.dueDate.id) { rDatesPromises.push(recurringDatesService.remove(corpTax.dueDate.id)) }
    if (corpTax.advancedPayments && corpTax.advancedPayments.id) { rDatesPromises.push(recurringDatesService.remove(corpTax.advancedPayments.id)) }

    return Promise.all(rDatesPromises)
      .then(() => {
        var postData = {corpTax: {taxNumber: null}}
        return companyService.update(state.selectedCompany, postData)
      })
      .then(company => {
        commit('setSelectedCompany', company)
        return company
      })
  },

  uploadSCLogo({ commit, state }, imgFile) {
    return companyService.uploadLogo(state.selectedCompany, imgFile)
      .then(thenHandler(commit, 'setSCLogo'))
  },

  deleteSCLogo({ commit, state }) {
    return companyService.deleteLogo(state.selectedCompany)
      .then(thenHandler(commit, 'setSCLogo'))
  },


  // ALIASES
  createAlias({ commit, state }, {refCompany, aname}) {
    return aliasService.create(state.selectedCompany, refCompany, aname)
  },
  createSCAlias({ commit, state }, aname) {
    return aliasService.create(state.selectedCompany, state.selectedCompany, aname)
      .then(thenHandler(commit, 'addSCAlias'))
  },
  updateSCAlias({ commit }, {alias, updatedAname}) {
    return aliasService.update(alias, updatedAname)
      .then(() => {
        commit('updateSCAlias', {alias, updatedAname})
        return true
      })
  },
  deleteSCAlias({ commit }, model) {
    return aliasService.delete(model)
      .then(() => {
        commit('deleteSCAlias', model)
      })
  },

  // TODO: refactor - generate some of the actions andmutations

  // VAT
  // createSCVat({ commit, state }, modelData) {
  //   return vatService.create(state.selectedCompany, modelData)
  //     .then(thenHandler(commit, 'addSCVat'))
  // },
  // updateSCVat({ commit }, newModel) {
  //   return vatService.update(newModel)
  //     .then(() => {
  //       commit('updateSCVat', newModel)
  //       return true
  //     })
  // },
  // deleteSCVat({ commit }, model) {
  //   return vatService.delete(model)
  //     .then(() => {
  //       commit('deleteSCVat', model)
  //     })
  // },


  // PERSONNEL
  // createSCPersonnel({ commit, state }, modelData) {
  //   return personnelService.create(state.selectedCompany, modelData)
  //     .then(thenHandler(commit, 'addSCPersonnel'))
  // },
  // updateSCPersonnel({ commit }, newModel) {
  //   return personnelService.update(newModel)
  //     .then(() => {
  //       commit('updateSCPersonnel', newModel)
  //       return true
  //     })
  // },
  // deleteSCPersonnel({ commit }, model) {
  //   return personnelService.delete(model)
  //     .then(() => {
  //       commit('deleteSCPersonnel', model)
  //     })
  // },


  // COMPANY PROPERTY PAIR
  // createSCCPPair({ commit, state }, modelData) {
  //   return companyPropertyService.create(state.selectedCompany, modelData)
  //     .then(thenHandler(commit, 'addSCCPPair'))
  // },
  // updateSCCPPair({ commit }, newModel) {
  //   return companyPropertyService.update(newModel)
  //     .then(() => {
  //       commit('updateSCCPPair', newModel)
  //       return true
  //     })
  // },
  // deleteSCCPPair({ commit }, model) {
  //   return companyPropertyService.delete(model)
  //     .then(() => {
  //       commit('deleteSCCPPair', model)
  //     })
  // },

}


var companyPropertiesData = [
  {// VAT
    name: 'Vat',
    service: vatService,
  },
  {// PERSONNEL
    name: 'Personnel',
    service: personnelService,
  },
  {// COMPANY PROPERTY PAIR
    name: 'CPPair',
    service: companyPropertyService,
  },
]

companyPropertiesData.reduce((_actions, item) => {
  // Create
  _actions['createSC' + item.name] = function({ commit, state }, modelData) {
    return item.service.create(state.selectedCompany, modelData)
      .then(thenHandler(commit, 'addSC' + item.name))
  }

  // Update
  const updateName = 'updateSC' + item.name
  _actions[updateName] = function({ commit }, newModel) {
    return item.service.update(newModel)
      .then(() => {
        commit(updateName, newModel)
        return true
      })
  }

  // Delete
  const deleteName = 'deleteSC' + item.name
  _actions[deleteName] = function({ commit }, model) {
    return item.service.delete(model)
      .then(() => {
        commit(deleteName, model)
      })
  }

  return _actions
}, actions)


export default {
  state,
  getters,
  mutations,
  actions,
}



