import merge from 'lodash.merge'

import basePropMixin from './basePropMixin'
import objUtils from '@/helpers/objUtils'
import {en, bg} from 'vuejs-datepicker/dist/locale'

export default function(setName) {
  const setObj = {
    data() {
      return {
        cpModel: Object.assign({}, this.cProp),
        editMode: !objUtils.hasAnyData(this.cProp),
        locales: {
          en: en,
          bg: bg,
        },
      }
    },
    // It may be needed in case the company data changes and the component need to be in Edit/Show mode accordingly
    // watch: {
    //   cProp: function value(newVal, oldVal) {
    //     console.log('* * * : ', newVal)
    //     const hasAnyData = newVal ? objUtils.hasAnyData(newVal) : false
    //     this.editMode = !hasAnyData
    //   },
    // },
    methods: {
      validate() {
        if (objUtils.hasAnyData(this.cpModel)) {
          return Promise.resolve(true)
        }
        return Promise.reject(new Error('Please, enter some data.'))
      },
      save() {
        var updateData = {}
        updateData[setName] = this.cpModel
        return this.$store.dispatch('updateSelCompany', updateData)
          .then((res) => {
            this.$emit('success')
            return res
          })
      },
      remove() {
        var updateData = {}
        updateData[setName] = Object.keys(this.cProp).reduce((obj, key) => {
          obj[key] = null
          return obj
        }, {})

        return this.$store.dispatch('updateSelCompany', updateData)
          .then(() => {
            this.$emit('remove')
          })
      },
    },
  }

  return merge({}, basePropMixin, setObj)
}



