import axios from 'axios'
import {apiToCompany, apiToCompanyShort, companyToApi} from './converters/companyConverters'
import recurringDatesService from './recurringDatesService'

const COMPANIES_API = '/Companies/'


export default {
  // LIST OF MY COMPANIES
  getMyCompanies() {
    return axios.get(COMPANIES_API)
      .then(data => data.map(apiToCompanyShort))
  },
  // CRUD
  create(companyData) {
    const postData = {
      name: companyData.name,
      addressText: companyData.address,
      countryId: companyData.country.id,
      parentFromDate: companyData.parentFromDate,
      currencyId: companyData.currency.id,
    }

    if (companyData.regNum) {
      postData.registrationNumber = companyData.regNum
    }

    if (companyData.parent) {
      postData.parentId = companyData.parent.id
    }
    return axios.post(COMPANIES_API, postData)
      .then(apiToCompany)
  },
  getCompany(company) {
    return axios.get(COMPANIES_API + company.id)
      .then(apiToCompany)
  },
  update(company, updatedData) {
    const postData = companyToApi(updatedData)
    let recurringDatesPromises = []

    if (postData.recurringDates) {
      recurringDatesPromises.push(recurringDatesService.bulkUpdate(postData.recurringDates))
    }

    return Promise.all(recurringDatesPromises)
      .then(() => {
        return axios.patch(COMPANIES_API + company.id, postData.company)
      })
      .then(companyData => {
        // TODO: ?? what will happen with the recurringDates?
        console.log('Kakvo vrusta server-a?', companyData)
        return apiToCompany(companyData)
      })
  },
  delete(company) {
    return axios.delete(COMPANIES_API + company.id)
  },
  // Misc
  uploadLogo(company, file) {
    return axios.post(COMPANIES_API + company.id + '/Logo', file)
  },
  deleteLogo(company) {
    return axios.delete(COMPANIES_API + company.id + '/Logo')
  },
}



