import Multiselect from 'vue-multiselect'
import {MONTHLY_DATES} from '@/nomenclaturesConstants'

export default {
  components: {
    Multiselect,
  },
  data() {
    return {
      toggle: false,
      errMsg: null,
      formData: {
        date: '',
        months: [],
        output: '',
        id: null,
      },
      dateOptions: MONTHLY_DATES,
      months: [
        { name: 'January', value: 'Jan' },
        { name: 'February', value: 'Feb', mark: true },
        { name: 'March', value: 'Mar' },
        { name: 'April', value: 'Apr', mark: true },
        { name: 'May', value: 'May' },
        { name: 'June', value: 'Jun', mark: true },
        { name: 'July', value: 'Jul' },
        { name: 'August', value: 'Aug' },
        { name: 'September', value: 'Sep', mark: true },
        { name: 'October', value: 'Oct' },
        { name: 'November', value: 'Nov', mark: true },
        { name: 'December', value: 'Dec' },
      ],
    }
  },
  props: {
    disabled: {
      type: Boolean,
      default: false,
    },
    value: {},
    singleMonth: {
      type: Boolean,
      default: false,
    },
  },
  created() {
    this.formData = this.objectToForm(this.value)
  },
  computed: {
    output() {
      let outputStr = this.formData.date ? 'Every ' + this.formData.date.name + ' of ' : 'Select recurring date'
      if (this.formData.date && this.formData.months && this.formData.months.length) {
        this.formData.months.forEach((month) => {
          outputStr += month.name + ', '
        })
        outputStr = outputStr.slice(0, -2)
      } else if (this.singleMonth && this.formData.date) {
        outputStr += this.formData.months.name
      }
      return outputStr
    },
  },
  methods: {
    confirm(evt) {
      let self = this
      evt.preventDefault()
      self.$validator.validateAll()
        .then(result => {
          if (result) {
            return Promise.resolve()
          }
          return Promise.reject(new Error('Please, fill the required fields.'))
        })
        .then(() => {
          self.toggle = false
          self.$emit('input', self.formToObject(self.formData))
        })
        .catch(() => {
          // TODO Show Error or something
        })
    },
    objectToForm(obj) {
      let self = this
      let output = {}
      output.months = []

      if (obj) {
        if (!self.singleMonth) {
          obj.months.split('').forEach((flag, index) => {
            if (Number(flag)) {
              output.months.push(self.months[index])
            }
          })
        } else {
          obj.months.split('').forEach((flag, index) => {
            if (Number(flag)) {
              output.months = self.months[index]
            }
          })
        }
        output.date = (obj.dayOfMonth === 0) ? self.dateOptions[31] : self.dateOptions[obj.dayOfMonth - 1]
        output.id = obj.id
        output.companyId = obj.companyId
      }
      return output
    },
    formToObject(formData) {
      let output = {}
      output.id = formData.id
      output.companyId = formData.companyId
      output.dayOfMonth = formData.date.value
      output.months = ''
      this.months.forEach(month => {
        if (!this.singleMonth) {
          output.months += (formData.months.filter((m) => {
            return m.name === month.name
          }).length) ? '1' : '0'
        } else {
          output.months += formData.months.name === month.name ? '1' : '0'
        }
      })
      return output
    },
    open() {
      this.toggle = true
    },
    close() {
      this.toggle = false
      this.$emit('close')
    },
  },
  watch: {
    formData: {
      handler(value) {
        let disable = false
        if (value.date && value.date.value === 31) {
          disable = true
        }
        this.months.forEach((month) => {
          month.disabled = month.mark && disable
        })
        if (value.date && (value.date.value === 30 || value.date.value === 29)) {
          disable = true
          this.months[1].disabled = true
        }

        if (disable) {
          if (!this.singleMonth) {
            value.months.forEach((current, index) => {
              if (current.disabled) {
                value.months.splice(index, 1)
              }
            })
          } else {
            value.months = value.months.disabled ? this.months[0] : value.months
          }
        }
      },
      deep: true,
    },
    value: {
      handler(val) {
        this.formData = this.objectToForm(val)
      },
      deep: true,
    },
  },
}



