// import dateUtils from '@/helpers/dateUtils'

function apiToRecurringDates(data) {
  return {
    id: data.id,
    companyId: data.companyId,
    typeId: data.typeId,
    typeName: data.typeName,
    targetId: data.targetId,
    dayOfMonth: +data.dayOfMonth,
    months: data.months,
    holidayHandlingTypeName: data.holidayHandlingTypeName,
    holidayHandlingTypeId: data.holidayHandlingTypeId,
  }
}

function recurringDatesToApi(data) {
  let obj = {
    typeId: data.typeId,
    dayOfMonth: data.dayOfMonth || 0,
    months: data.months ? data.months : '000000000000',
  }

  if (data.id) { obj.id = data.id }
  if (data.companyId) { obj.companyId = data.companyId }
  if (data.typeName) { obj.typeName = data.typeName }
  if (data.targetId) { obj.targetId = data.targetId }
  if (data.holidayHandlingTypeName) { obj.holidayHandlingTypeName = data.holidayHandlingTypeName }
  if (data.holidayHandlingTypeId) { obj.holidayHandlingTypeId = data.holidayHandlingTypeId }

  return obj
}

export {
  apiToRecurringDates,
  recurringDatesToApi,
}



