import Multiselect from 'vue-multiselect'
import {MONTHLY_DATES, MONTHS} from '@/nomenclaturesConstants'
// import merge from 'lodash.merge'

function formDataToModel(formData, monthSelection) {
  var model = {}
  var monthArr

  model.id = formData.id
  model.companyId = formData.companyId
  model.dayOfMonth = formData.date.value

  if (Array.isArray(monthSelection)) {
    monthArr = monthSelection.map(month => {
      return month.selected ? 1 : 0
    })
  } else {
    monthArr = new Array(12).fill(0);
    monthArr[monthSelection] = 1
  }

  model.months = monthArr.join('')

  return model
}

function setDateStr(formData) {
  if (formData.date) {
    var str =  'Every ' + formData.date.name + ' of '

    if (formData.months && Array.isArray(formData.months)) {
      str += formData.months.map(month => {
        return month.name
      }).join(', ')
    } else {
      str += formData.months.name
    }

    return str
  }

  return 'Select recurring date'
}

export default {
  components: {
    Multiselect,
  },
  props: {
    disabled: {
      type: Boolean,
      default: false,
    },
    value: {},
    singleMonth: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      toggle: false,
      errMsg: null,
      formData: {
        date: '',
        months: [],
        id: null,
      },
      selectedSingleMonth: null,
      recurringDateStr: '',
      dateOptions: MONTHLY_DATES,
      months: MONTHS,
      // months: merge({}, MONTHS),
    }
  },
  created() {
    this.formData = this.modelToFormData(this.value)
    this.recurringDateStr = setDateStr(this.formData)
  },
  methods: {
    onDateChange(date) {
      var self = this

      for (var i = 11; 0 < i; i--) {
        var month = self.months[i]
        month.disabled = !(month.days >= date.value)

        if (month.disabled) {
          if (self.singleMonth) {
            // TODO: da se vidi, kogato e s radiobutoni
            if (self.selectedSingleMonth === i) {
              // This is the selected month, that needs to be disabled, so remove the selection
              self.selectedSingleMonth = null
              self.formData.months = null
            }
          } else {
            month.selected = false

            for (var y = self.formData.months.length; y--;) {
              if (self.formData.months[y].disabled) {
                self.formData.months.splice(y, 1)
                break
              }
            }
          }
        }
      }
    },
    onMonthChange(index) {
      var self = this
      if (self.singleMonth) {
        self.selectedSingleMonth = index
      } else {
        var month = this.months[index]
        month.selected = !month.selected
      }
    },
    confirm(evt) {
      let self = this
      evt.preventDefault()
      self.$validator.validateAll()
        .then(result => {
          if (result) {
            return Promise.resolve()
          }
          return Promise.reject(new Error('Please, fill the required fields.'))
        })
        .then(() => {
          self.toggle = false
          var selection = self.singleMonth ? self.selectedSingleMonth : this.months
          self.$emit('input', formDataToModel(self.formData, selection))
        })
        .catch(() => {
          // TODO Show Error or something
        })
        .finally(() => {
          self.recurringDateStr = setDateStr(self.formData)
        })
    },
    open() {
      this.toggle = true
    },
    close() {
      this.toggle = false
      this.$emit('close')
    },
    modelToFormData(model) {
      let self = this
      let form = {}
      form.months = []

      if (model) {
        form.id = model.id
        form.companyId = model.companyId
        form.date = (model.dayOfMonth === 0) ? self.dateOptions[31] : self.dateOptions[model.dayOfMonth - 1]

        model.months.split('').forEach((flag, i) => {
          if (+flag) {
            if (self.singleMonth) {
              form.months = self.months[i]
            } else {
              form.months.push(self.months[i])
            }
          }
        })
      }

      return form
    },
  },
  watch: {
    // immediate: true, // TODO: should work instead of created()
    value: {
      handler(model) {
        if (model && model.companyId !== this.formData.companyId) {
          this.formData = this.modelToFormData(model)
          this.recurringDateStr = setDateStr(this.formData)
        }
      },
      deep: true,
    },
  },
}



