import axios from 'axios'
import {apiToRecurringDates, recurringDatesToApi} from './converters/recurringDatesConverter'

const RECURRING_DATES_API = '/RecurringDates/'

export default {
  create(formData) {
    const postData = recurringDatesToApi(formData)
    return axios.post(RECURRING_DATES_API, postData)
    // .then(apiToRecurringDates)
  },
  get(recurringDatesID) {
    return axios.get(RECURRING_DATES_API + recurringDatesID)
      .then(apiToRecurringDates)
  },
  bulkUpdate(recurringDates) {
    // const {
    //   "companyId": companyID,
    //   "targetId": companyID,
    //   'holidayHandlingTypeId': 0
    // }
    // merge({}, recurringDates, setObj)
    // return axios.patch(RECURRING_DATES_API + recurringDatesID, postData)
    // return axios.patch(RECURRING_DATES_API, recurringDates)

    let promises = recurringDates.map(date => {
      axios.patch(RECURRING_DATES_API + date.id, date)
    })

    // TODO: is then() needed?
    return Promise.all(promises)
  },
  remove(recurringDatesID) {
    return axios.delete(RECURRING_DATES_API + recurringDatesID)
  },
}



